<template>
    <div class="reg">
      <div class="form-group">
          <span class="form-title">
              注册账号
          </span>
          <div class="icon">
              <input type="file" class="icon-btn" ref="icon">
              <img src="http://static.missevan.com/assets/images/home/default-icon.jpg" alt="" class="icon-img" @click="icon">
              <span>上传头像</span>
          </div>
          <input type="text" placeholder="昵称" v-model="nikename">
          <input type="text" placeholder="账号" v-model="username">
          <input type="text" placeholder="密码" v-model="password" >
          <p class="tip">{{errorMess}}</p> 
          <button class="reg-btn" @click="reg">注册</button> 
          <div class="page-bottom">
             
              <span>完成注册即代表您已同意</span>
            <div class="br">
              <p>用户协议</p>
              <span>和</span>
              <p>隐私政策</p>
            </div>
          </div>
          <div class="bottom">
              <a>用邮箱注册</a>
              <router-link to="/login" class="login-back">返回登录></router-link>
          </div>
      </div>
      
    </div>
</template>
<script>
import Vue from 'vue';
import { Dialog } from 'vant';
export default {
  name:'reg',
  props:[],
  data(){
      return{
        username:'',
        password:'',
        nikename:'',
        errorMess:''
      }
  },
  components: {
    [Dialog.Component.name]: Dialog.Component,
  },
  methods:{ 
      icon(){
          this.$refs.icon.click()
      },
       reg(){
        let formData = new FormData();
        formData.append('username',this.username);
        formData.append('password',this.password);
        formData.append('nikename',this.nikename);
        
        let iconInputFile = document.getElementsByClassName('icon-btn')[0];
        formData.append('icon',iconInputFile.files[0]);//取出input file的文件体
        
        this.$axios({
          url:'/api/reg',
          method:'post',
          data:formData
        }).then(
          res => {
            if(res.err === 0){
              
              Dialog.alert({
                message: '注册成功,请去登录',
                theme: 'round-button',
                }).then(() => {
                this.$router.push('/login')
                });
            }else{
              this.errorMess=res.msg
            }
          }
        )
    },
  }, 
  

}  
</script>
<style scoped lang="less">
@import url('../style/index.less');
.reg{
    margin-top: 50px;
    margin-bottom: 35px;
    .form-group{
        margin: 10px auto;
        width: 293px;
        .form-title{
            font-size: 16px;
            margin: 0 auto;
            color: #000;
            display: block;
        }
        .icon{
            font-size: 12px;
            margin: 0 auto;
            border: 1px solid #e0e0e0;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            
            span{
                position: relative;
                top: -100px;
                left: 15px;
                color:#9e9e9e;
            }
            .icon-img{
                width: 100%;
                height: 100%;
                border-radius: 50%;
                position: relative;
                top: -100px;
            }
        }
        .icon-btn{
        
             width: 50px;
            height: 50px;
            opacity: 0;
        }
         input{
            font-size: 14px;
            width: 283px;
            margin: 25px auto;
            display: block;
            height: 42px;
            line-height: 30px;
            padding-left:10px;
            border: 1px solid #e0e0e0;
            border-radius: 4px;
            outline: none;  
        }
        .tip{
            margin-top: -20px;
            color: red;
            font-size: 14px;
        }
        .reg-btn{
            width: 293px;
            height: 40px;
            margin: 10px auto;
            line-height: 40px;
            font-size: 16px;
            color: #fff;
            background-color: #c14a3f;
            border-color: #c14a3f;
            border: 1px solid transparent;
            border-radius: 3px;
            outline: none;
        }
        .page-bottom{
                text-align: center;
                span{
                    color: #9e9e9e;
                    // display: inline-block;
                    font-size: 14px;
                      
                }
                p{
                    color: #424242;
                    display: inline-block;
                    font-size: 14px;
                }
                .br{
                    margin-top: -28px;
                    span{
                        margin: 0 5px;
                    }
                }
        }
        .bottom{
            font-size: 14px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin: 20px 0;
            a{
                color: #9e9e9e;
                display: block;
            }
            .login-back{
                color: #000;
            }
        }
    }
}


</style>
