import axios from '../utils/axios.js'
// import jsonp from 'jsonp'
// 轮播图的数据
export const getData1=()=>{
	return new Promise((resolve,reject)=>{
		let url='/api/goods/recommend'
		axios.get(url)
		.then((res)=>{
			resolve(res)	
		})
		.catch((err)=>{
			reject(err)
		})	
	})
}
