<template>

    <div class="login">
        <div class="form-group">
            <span class="form-title">
                登录 猫耳FM
            </span>
            <input type="text" placeholder="账号" v-model="username">
            <div class="form-row">
                <input type="text" placeholder="密码" v-model="password">
                <a href="" class="row-right">忘记密码？</a>
            </div>
            <p class="tip">{{errorMess}}</p>
            <button class="submit-btn btn" @click="login">
                登录
            </button>
            <router-link to="/reg">
                <button class="reg-btn btn">
                    注册
            </button>   
            </router-link>
            <div class="page-bottom">
                <span>登录即代表您已同意</span>
                <p>用户协议</p> 
                <span>和</span>
                <p>隐私政策</p>
            </div>
            <div class="login-bottom">
                <img src="../../public/login-bottom.png" alt="">
            </div>
        </div>
    </div>

</template>
<script>

export default {
  name:'login',
  props:[],
  
  data(){
      return{
        username:'',
        password:'',
        errorMess:''//登录失败信息
      }
  },
  methods:{ 
       login(){
        this.$axios({
          url:'/api/login',
          data:{username:this.username,password:this.password},
          method:'post',
        }).then(
          res => {
            if(res.err == 0){
              // 1.种token
              window.localStorage.setItem('user',JSON.stringify(res))
              this.$root.user = res.data
              // 2.跳转到之前,没有之前跳转到user
              if(this.$route.query.p){
                this.$router.replace(this.$route.query.p)
              }else{
                this.$router.replace('/recommend')
              }
            }else{
              this.errorMess = res.msg
            }
          }
        )
        this.$root.bNickname1=this.$root.bImg1=false
        this.$root.bNickname2=this.$root.bImg2=true
        this.$root.bLogout=true
      }
  },  
}  
</script>
<style scoped lang="less">
@import url('../style/index.less');

.login{
    background: rgb(245, 245, 245);
    margin-top: 45px;
    margin-bottom: 55px;
    .form-group{
        margin: 0 auto;
        width: 293px;
        .form-title{
            font-size: 16px;
            margin: 12px 0;
            color: #000;
        }
        input{
            width: 283px;
            margin: 13px 0;
            display: block;
            height: 42px;
            line-height: 30px;
            padding-left:10px;
            border: 1px solid #e0e0e0;
            border-radius: 4px;
            outline: none;
            font-size: 14px;
        }
        .form-row{
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            position: relative;
            margin: 10px 0;
            .row-right{
                top: 0;
                bottom: 0;
                height: 14px;
                margin: auto;
                padding-right: 9px;
                right: 0;
                position: absolute;
                font-size: 14px;
                line-height: 14px;
                color: #bdbdbd;
                border: none;
            }
        }
        .tip{
            margin-top: -20px;
            color: red;
            font-size: 14px;
        }
        .btn{
            width: 100%;
            height: 40px;
            margin: 10px 0;
            line-height: 40px;
            font-size: 16px;
            color: #fff;
            background-color: #c14a3f;
            border-color: #c14a3f;
            display: inline-block;
            position: relative;
            text-align: center;
            vertical-align: middle;
            cursor: pointer;
            border: 1px solid transparent;
            border-radius: 3px;
        }
        .reg-btn{
            background-color: #fff;
            border-color: #e0e0e0;
            color: #000;
        }
        .page-bottom{
            text-align: center;
            span{
                display: inline-block;
                font-size: 14px;
                color: #9e9e9e;
                margin: 0 5px;
            }
            p{
                display: inline-block;
                color: #424242;
                font-size: 14px;
                
            }
        }
        .login-bottom{
            margin-top: 20px;
            text-align: center;
            margin-right: 20px;
            img{
                width: 110%;
                height: 110%;
                
            }
        }
        
    }
}

</style>
