<template>
  <div id="userBar" class="show" :style="{left:data}">
    <a class="row-user" @click="jump();back()">
      <div class="icon">
        <img v-show="$root.bImg2" :src="$baseUrl+$root.user.icon" alt="" class="cover">
        <img v-show="$root.bImg1" src="//static.missevan.com/assets/images/home/default-icon.jpg" alt="" class="cover">
      </div>
      <div class="details">
        <span class="username">
          <p v-show="$root.bNickname2">{{$root.user.nikename}}</p> 
          <p v-show="$root.bNickname1">点击登录</p>
          <span class="gt">&gt;</span>
        </span>
        <span class="info">
            <p class="dimond " v-show="$root.bImg2">钻石: {{$root.user.fans}}</p>
            <p class="fish" v-show="$root.bNickname2">小鱼干: {{$root.user.follow}}</p>
        </span>
      </div>
    </a>
    <a href="javascript:;" class="station">
      <i class="home icon-img1"></i>主站 
      <span>&gt;</span> 
    </a>
    <a href="https://fm.missevan.com/" class="station">
      <i class="home icon-img2"></i>直播 
      <span>&gt;</span> 
    </a>
    <a href="" class="station">
      <i class="home icon-img3"></i>钱包 
      <span>&gt;</span> 
    </a>
    <span v-show="$root.bLogout" class="logout row" @click="logout();back()">退出登录</span>
  </div> 
  
</template>

<script>
export default {
  name:'user',
  props:['data'],
  computed:{ },
  mounted(){
    this.$root.user = JSON.parse(window.localStorage.getItem('user').data)
    this.$root.bNickname1=this.$root.bImg1=false
    this.$root.bNickname2=this.$root.bImg2=true
    this.$root.bLogout=true
  }, 
  methods:{
    jump(){
      if(window.localStorage.getItem('user')){
       this.$router.push("/")
      }else{
        this.$router.push("/login")
      }
    },
    back(){
        this.$emit("back")
    },
    logout(){
        window.localStorage.removeItem('user');
        this.$router.push('/recommend');
        this.$root.user ={}
        this.$root.bNickname1=this.$root.bImg1=true
        this.$root.bNickname2=this.$root.bImg2=false
        this.$root.bLogout=false
      }

  },
}
   
</script>


<style scoped lang="less">
@import url('../style/index.less');
#userBar{
    position: fixed;
    z-index: 2;
    .t(37);
    width: 100%;
    height: calc(100% - 40px);
    transition: left .5s ease;
    background-color: @black_2; 

    .row-user{
      position: relative;
      background-color: #3d3d3d;
      color: #fff;
      display: flex;
      .a_i();
      height: auto;
      .m_b(5);
      padding: 17px 14px 16px;
      .icon{
        position: relative;
        .w(60);
        .h(60);
        .cover{
          width: 100%;
          height:100%;
          border-radius: 50%;
        }
      }
      .details{
            padding-left: 15px;
            display: flex;
            flex-direction: column;
            justify-content:center;
        .username{
          display: flex;
          align-items: center;
          font-size: @fs-u;
          .gt{
            position: absolute;
            right: 8px;
          }
        }
        .info{
          margin-top:10px;
          display: flex;
          justify-content: space-between;
          p{
          margin-right: 10px;
          min-width: 100px;
          color: #bdbdbd;
          font-size: 13px;
          } 
        }
      }
    }
    .station{
      position: relative;
      .p_l(42);
      font-size: @fs-nav;
      width: 100%;
      height: 50px;
      .l_h(50);
      color: @bg_tab;
      display: block;
      background-color: #3d3d3d;
      &::before{
            content: "";
            position: absolute;
            .b(0);
            width: 100%;
            .h(1);
            background-color: #1b1b1b;
      }
      span{
        display: inline-block;
        position: absolute;
        .r(50);
      }
      .home{
          .w(24);
          .h(24);
          position: absolute;
          top: 25%;
          .l(-10);
          margin: 0px 20px;
      }
      .icon-img1{
          background-image: url("../../public/sprite-icons.png");
          background-position: -228px -133px;
          background-size: 281px 262px;
      }
      .icon-img2{
          background-image: url("../../public/sprite-icons.png");
          background-position:-228px -107px;
          background-size: 281px 262px;
      }
      .icon-img3{
          background-image: url("../../public/sprite-icons.png");
          background-position:-228px -81px;
          background-size: 281px 262px;
      }

    }
    .logout{
 
          display: block;
          margin-top: 30px;
          line-height: 50px;
          text-align: center;
          color: #fff;
          font-size: 16px;
         

    }
    .row{
         position: relative;
          width: 100%;
          height: 50px;
          background-color: #3d3d3d;
    }
}




</style>
