<template>
  <div class="app">
   <my-header @change="change"/>
   <loading></loading>
   
   <my-tab v-show="$root.bTab"/>
   <user :data='num' @back='back'></user>
   
  <keep-alive>
     <router-view></router-view>
  </keep-alive>
   <my-footer></my-footer>
  </div>
</template>

<script>
import loading from './components/loading'
import myHeader from './components/my-header.vue'
import myTab from './components/my-tab.vue'
import myFooter from './components/my-footer'
import user from '@/pages/user'

export default {
  components:{myHeader,myTab,myFooter,user,loading},
  name: 'app',
  data(){
    return {
      num:'100%'
    }
  },
  methods:{
    change(data,type){

      type ? this.num=data :this.num = '100%'
    },
    back(){
      this.num = '100%'
    },
     checkRootData(path){
        if(/login|reg/.test(path)){
          this.$root.bTab=false
        }else{
          this.$root.bTab=true
        }
      }
  },
   watch:{
    	$route:{
        handler(nextValue){
          let path = nextValue.path;
          this.checkRootData(path)
        },
      }
    }
}
</script>

<style lang="less" scoped>
@import url('./style/index.less');

</style>
