<template>
  <div class="recommend">
	<div class="banner">
		<my-banner :Banner="banner"></my-banner>
	</div>
	<m-sound :Msound="mSound" text1='人气M音' text2='排行榜' collectionName='soundone'></m-sound>
	<m-sound :Msound="mCartoon" text1='有声漫画' text2='更多' collectionName='sound'></m-sound>
	<m-sound :Msound="broadcast" text1='广播剧' text2='更多' collectionName='sound'></m-sound>
	<m-sound :Msound="music" text1='音乐' text2='更多' collectionName='sound'></m-sound>
	<m-sound :Msound="lover" text1='声音恋人' text2='更多' collectionName='sound'></m-sound>
	<m-sound :Msound="ent" text1='娱乐' text2='更多' collectionName='sound'></m-sound>
	<m-sound :Msound="grab" text1='日抓' text2='更多' collectionName='sound'></m-sound>
	<m-sound :Msound="book" text1='听书' text2='更多' collectionName='sound'></m-sound>
	<m-sound :Msound="ring" text1='铃声' text2='更多' collectionName='sound'></m-sound>
	<m-sound :Msound="hyp" text1='催眠' text2='更多' collectionName='sound'></m-sound>
  </div>
</template>

<script>
import myBanner from '@/components/my-banner'
import mSound from '../components/m-Sound'
import {getData1} from '../api'
import {getData2} from '../api'
import {getData5} from '../api'
	export default {
		  data(){
        return {
			banner:[],
			mSound:[],
			mCartoon:[],
			broadcast:[],
			music:[],
			lover:[],
			ent:[],
			grab:[],
			book:[],
			ring:[],
			hyp:[]
			
        }
    },
		components:{myBanner,mSound},		
		created(){
				getData1().then((res)=>{this.banner=res.data[0].info.banner;})
				getData5().then((res)=>{this.mSound=res.data.splice(',',3)})
				getData2().then((res)=>{
					
					this.mCartoon=res.data.splice(',',3)
					this.broadcast=res.data.splice(',',3)
					this.music=res.data.splice(',',3)
					this.lover=res.data.splice(',',3)
					this.ent=res.data.splice(',',3)
					this.grab=res.data.splice(',',3)
					this.book=res.data.splice(',',3)
					this.ring=res.data.splice(',',3)
					this.hyp=res.data.splice(',',3)
					})		
		},	
	}
</script>


<style scoped lang="less">
@import url('../style/index.less');
.recommend{
	.banner{
	.w(375);
	.h(125);
	}	
}
</style>
